<template>
  <div class="collection">
    <!---->
    <div class="home clearfix">
      <a href="/"></a>
      <a @click="previousPage"></a>
    </div>
    <!--    -->
    <div class="load-state" v-if="collectionList.length===0">
        <p class="load-pic">
          <img src="../../assets/images/load-kong.png" alt="">
        </p>
        <p>~~</p>
        <p class="load-open">
          <a href="/"></a>
        </p>
    </div>

    <!---->
    <ul class="clearfix" v-if="collectionList.length!=0">
      <li v-for="item in collectionList" :key="item.book_id">
        <img :src="item.book_img" @click="bookInfo(item.book_id)">
        <h4>{{item.book_name}}</h4>
        <div class="info">
            <span>
              {{item.collection_time|dateFormat1}}
            </span>
        </div>
        <el-button type="danger" size="mini" class="cancelbutton" @click="deleteCollection(item.id)"></el-button>
      </li>
    </ul>

    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total" v-if="collectionList.length!=0">
    </el-pagination>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    return {
      //
      user: {},
      //
      queryInfo: {
        //id
        user_id: '',
        //
        pagenum: 1,
        //
        pagesize: 5,
      },
      //
      collectionList: [],
      //
      total: 0
    }
  },
  created() {
    //sessionStorage
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //
    this.getCollectionList()
  },
  methods: {
    //
    getCollectionList() {
      this.queryInfo.user_id = this.user.user_id
      this.$http.get('/user/getCollectionList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.collectionList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //id
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //
    async deleteCollection(id) {
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.get('/user/deleteCollectionById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCollectionList()
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCollectionList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCollectionList()
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.load-state .load-open a{
  color: #28e;
}

.load-state .load-open{
  font-size: 14px;
  font-weight: 400;
}

.load-state{
  padding: 100px;
  text-align: center;
  font-weight: 700;
}

.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}
.collection{
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.el-pagination {
  text-align: center;
}



.collection ul {
  width: 1200px;
  margin-top: 10px;
}

.collection ul li {
  float: left;
  width: 225px;
  height: 310px;
  background-color: #FFFFFF;
  margin-right: 8px;
  margin-bottom: 15px;
  border: 1px solid #f3f3f3;
}

.collection ul li:hover {
  cursor:pointer;
  z-index: 2; /**/
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1); /**/
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0); /**/
  transform: translate3d(0, -2px, 0);
}

.collection ul li > img {
  width: 150px;
  height: 150px;
  margin: 20px 0 10px 35px;
}

.collection ul li h4 {
  height: 46px;
  padding: 10px 20px 0;
  font-size: 12px;
  font-weight: 400;
  /**/
  overflow: hidden;
  /**/
  text-overflow: ellipsis;
  /*  */
  display: -webkit-box;
  /*  */
  -webkit-line-clamp: 3;
  /*  */
  -webkit-box-orient: vertical;
}

.collection ul li h4:hover {
  color: #ff7c2d;
}

.collection ul li .info {
  margin: 15px 20px 0 25px;
}

.collection ul li .info span {
  font-size: 12px;
  color: #666;
}

.collection ul li .cancelbutton{
  float: right;
  margin-top: 5px;
  margin-right: 20px;
}
</style>
