<template>
  <div id="nav">
    <vue-particles
      color="#555"
      :particleOpacity="0.7"
      :particlesNumber="150"
      shapeType="circle"
      :particleSize="4"
      linesColor="#555"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="false"
      clickMode="push"
      class="lizi"
    />

    <div id="login-container">
      <div style="text-align: center;height: 50px;margin-left: 11%">
        
      </div>
      <el-form status-icon :model="loginForm" :rules="loginFormRules" ref="loginFormRef" label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="" prop="username">
          <el-input type="text" autocomplete="off" v-model="loginForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" autocomplete="off" v-model="loginForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="vercode">
          <el-input type="password" autocomplete="off" v-model="loginForm.vercode" style="width: 165px"
                    @keyup.enter.native="login"></el-input>
          <div style="float: right">
            <img :src="imageUrl" @click="change" style="width: 130px;height: 40px;">
          </div>
        </el-form-item>
        <el-form-item style="margin-top: -20px">
          <el-button type="primary" @click="login"></el-button>
          <el-button @click="resetLoginForm"></el-button>
          <el-link type="primary" @click="returnhome" style="margin-left: 10px"></el-link>
          <el-link type="primary" style="margin-left: 10px" @click="goRegister"></el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>

export default {
  name: "login",
  data() {
    return {
      //
      loginForm: {
        username: '',
        password: '',
        //
        vercode: ''
      },
      //
      loginFormRules: {
        username: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 10,
            message: ' 2  10 ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 11,
            message: ' 6  11 ',
            trigger: 'blur'
          }
        ],
        //
        vercode: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      //
      /*imageUrl: 'http://localhost:8080/bookstore_SSM_Vue/getVerifyCode'*/
      imageUrl: 'http://localhost:8080/getVerifyCode'
    }
  },
  methods: {
    //
    returnhome() {
      this.$router.push('/home')
    },
    //
    login() {
      //validate
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return

        await this.$http.get('/user/userLogin', {params: this.loginForm}).then(res => {
          console.log(res)
          if (res.data.data.vercodeError === "") {
            //
            this.change()
            //
            this.loginForm.vercode = ''
            return this.$message.error('')
          } else if (res.data.data.loginNo === "") {
            return this.$message.error('')
            //
            this.change()
          }

          //sessionStorage,JSON
          window.sessionStorage.setItem('user', JSON.stringify(res.data.data.loginYes))

          this.$message.success('')

          //
          this.$router.go(-1);

          /*var item = window.sessionStorage.getItem('user');
          console.log(JSON.parse(item))*/
        })
      })
    },
    //
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields()
    },
    //
    change() {
      let num = Math.ceil(Math.random() * 10);//
      this.imageUrl = 'http://localhost:8080/getVerifyCode?hyhh=' + num
    },
    //
    goRegister(){
      this.$router.push('/userregister')
    }
  }
}
</script>

<style scoped>
body {
  margin: 0;
}

#login-container {
  width: 400px;
  height: 290px;
  background: #e5e9f2;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -220px;
  margin-top: -170px;
  border-radius: 5px;
  padding-top: 40px;
  padding-right: 40px;
}

body {
  width: 100%;
  background-color: rgb(239, 239, 239);
}

.lizi {
  position: fixed;
  top: 0;
  width: 100%;
}

#particles-js {
  width: 100%;
}

</style>
