<template>
  <div class="order">
    <!---->
    <div class="home clearfix">
      <a href="/"></a>
      <a @click="previousPage"></a>
    </div>
    <!---->
    <!---->
    <el-row v-if="order.length>0">
      <el-col>
        <el-table :data="order" style="width: 100%">
          <!---->
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-row>
                <el-col :span="8">
                  <span>{{ scope.row.nick_name }}</span>
                </el-col>
                <el-col :span="8">
                  <span>{{ scope.row.tel }}</span>
                </el-col>
                <el-col :span="8">
                  <span>{{ scope.row.address }}</span>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <el-table-column prop="date" label="" width="300">
            <template slot-scope="scope">
              <a class="name-a" @click="getBookInfo(scope.row.book_id)">
                <img :src="scope.row.book_img" style="height: 92px;width: 60px"/>
                <span>{{ scope.row.book_name }}</span>
              </a>
            </template>
          </el-table-column>
          <el-table-column prop="buy_price" label="" width="130" sortable></el-table-column>
          <el-table-column prop="buy_num" label="" width="130"></el-table-column>
          <el-table-column prop="buy_time" label="" width="170" sortable>
            <template slot-scope="scope">
                <span>
                  {{ scope.row.buy_time|dateFormat1 }}
                </span>
            </template>
          </el-table-column>

          <el-table-column label="" width="180" class="el-icon-user">
            <template slot-scope="scope">
                            <span>
                                {{ (scope.row.buy_num * scope.row.buy_price).toFixed(2) }}
                            </span>
            </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.status===0">
                
              </span>
              <span v-if="scope.row.status===1">
                
              </span>
              <span v-if="scope.row.status===2">
                
              </span>
            </template>
          </el-table-column>
          <el-table-column label="" width="220">
            <template slot-scope="scope">
              <div v-if="scope.row.status===1">
                <el-button type="primary" size="mini" @click="receiving(scope.row.order_id)"></el-button>
              </div>

              <div v-if="scope.row.status===2">
                <el-button type="danger" @click="deleteOrderById(scope.row.order_id)" size="mini"></el-button>
                <el-button type="primary" @click="commentDialogBox(scope.row.book_id)" size="mini"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <!---->
    <div v-if="order.length===0">
      <div class="tips-words">
        <i></i>
        <div>
          <h3><a href="/"></a></h3>
          <dl>
            <dt>:</dt>
            <dd>1.</dd>
            <dd>2.</dd>
          </dl>
        </div>
      </div>
      <HomeFooter></HomeFooter>
    </div>

    <!---->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="commentVisible" width="30%">
      <el-input type="textarea" placeholder="" v-model="textarea" maxlength="30" show-word-limit></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="commentVisible = false" style=""> </el-button>
        <el-button type="primary" @click="addComment"> </el-button>
      </span>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";

export default {
  data() {
    return {
      //
      user: {},
      //
      queryInfo: {
        user_id: '',
        //
        pagenum: 1,
        //
        pagesize: 5,
      },
      //
      order: [],
      //
      total: 0,
      //
      commentVisible: false,
      //
      textarea: '',
      //id
      book_id: '',
      //
      addCommentForm: {
        user_id: '',
        user_name: '',
        comment_info: '',
        book_id: '',
        book_name: '',
        user_img: ''
      },
      //
      book: {},
    }
  },
  created() {
    this.user = JSON.parse(window.sessionStorage.getItem('user'));
    //
    this.getUserOrderList(this.user.user_id)
  },
  methods: {
    //
    getUserOrderList(user_id) {
      this.queryInfo.user_id = user_id
      this.$http.get('/user/getUserOrderList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.total = res.data.data.total
        this.order = res.data.data.list
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserOrderList(this.user.user_id)
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserOrderList(this.user.user_id)
    },
    //
    getBookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //
    home() {
      this.$router.push('/home')
    },
    //id
    async deleteOrderById(order_id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.get('/user/deleteOrderById?id=' + order_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getUserOrderList(this.user.user_id)
      })
    },
    //id
    commentDialogBox(book_id) {
      //id
      this.book_id = book_id
      //
      this.$http.get('/public/getBookDetails', {params: {book_id: this.book_id}}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.book = res.data.data.book
      })
      //
      this.commentVisible = true
    },
    //
    addComment() {
      if (this.textarea.trim() === '') {
        return this.$message.info('')
      }
      //sessionStorage
      var item = JSON.parse(window.sessionStorage.getItem('user'))


      /**/
      this.addCommentForm.user_id = item.user_id
      this.addCommentForm.user_name = item.username
      this.addCommentForm.comment_info = this.textarea.trim()
      this.addCommentForm.book_id = this.book_id
      this.addCommentForm.book_name = this.book.book_name
      this.addCommentForm.user_img = item.user_img

      this.$http.post('/user/addComment', this.addCommentForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('')
        }
        this.$message.success('')
        this.textarea = ''
        this.commentVisible = false
      })
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
    //
    receiving(order_id) {
      this.$http.get('/user/receiving?id=' + order_id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getUserOrderList(this.user.user_id)
      })
    }
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.name-a > span {
  float: left;
  width: 220px;
  height: 92px;
  overflow: hidden;
  text-overflow: ellipsis;
  /*  */
  display: -webkit-box;
  /*  */
  -webkit-line-clamp: 4;
  /*  */
  -webkit-box-orient: vertical;
}

.name-a > img {
  float: left;
}

.name-a {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.page {
  margin: 15px 0;
  text-align: center
}

.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}

.order {
  width: 1358px;
  margin: 0 auto;
}

.tips-words {
  width: 1200px;
  height: 300px;
  margin: 0 auto;
  margin-top: 10px;
  background-color: #FFF5ED;
}

.tips-words i {
  width: 54px;
  height: 68px;
  margin: 116px 0 116px 60px;
  background-position: -239px -152px;
  display: block;
  float: left;
  padding: 0 20px 0 0;
  background: url("https://res.suning.cn/project/search/searchpage_new/css/images/pic.png?20210126") -165px -153px no-repeat;;
}

.tips-words div {
  float: left;
  margin-top: 114px;
}

.tips-words div h3 a {
  color: #28e;
}

.tips-words div h3 {
  color: #666666;
}

.tips-words div dl {
  color: #666666;
  font-size: 12px;
}

.tips-words div dl dd em a {
  color: #666666;
  font-style: normal;
}


.no-item {
  margin: 50px 0 50px 0;
}

.no-cart > h2 {
  margin-left: 400px;
  font-size: 36px;
  color: #b0b0b0;
}

.no-cart > a {
  text-decoration: none;
  color: white;
  background-color: #ff6700;
  display: inline-block;
  margin-left: 450px;
  padding: 20px;
  width: 130px;
  text-align: center;
  border-radius: 20px;
  font-size: 20px;
}

.to-my-order {
  margin-right: 20px
}

.to-my-order:hover, .to-index:hover {
  color: #ff6700;
}

.my-cart-title {
  display: inline-block;
  color: #454545;
  font-size: 30px;
  margin-right: 20px;
}

a:hover {
  cursor: pointer;
}

.my-cart-subtitle {
  color: #828282;
}

.my-cart {
  background-color: white;
  height: 100px;
  line-height: 100px;
}

.my-cart a {
  text-decoration: none;
  color: #565656;
  font-size: 20px;
}

.to-my-order > a:hover {
  cursor: pointer;
}


.main-item {
  width: 1200px;
  margin: 0 auto;
  /*background-color: deepskyblue;*/
  height: 200px;
}

.book-item {
  width: 240px;
  height: 350px;
  background-color: white;
  margin: 0 20px 20px 20px;
  border: 1px none;
  text-align: center;
}

.book-item > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.book-item:hover {
  box-shadow: 10px 10px 10px 5px #e1e1e1;
}

.book-item img {
  display: block;
  margin: 20px auto;
  width: 220px;
  height: 220px;
}

.main-title {
  text-align: center
}

hr {
  width: 350px;
  display: inline-block;
  margin: 0 20px 10px 20px;
}

.main-title > h2 {
  color: #848484;
  text-align: center;
  display: inline-block;
}

.main-title > img {
  display: inline-block;
}
</style>
