<template>
  <div>
    <div class="cart">
      <!---->
      <div class="home clearfix">
        <a href="/"></a>
        <a @click="previousPage"></a>
      </div>

      <!---->
      <el-row v-if="carts.length>0">
        <el-row>
          <el-col>
            <el-table ref="multipleTable" :data="carts" tooltip-effect="dark" style="width: 100%"
                      @selection-change="handleSelectionChange">

              <el-table-column type="selection" width="55"></el-table-column>

              <el-table-column prop="book_name" label="" width="400">
                <template slot-scope="scope">
                  <a class="name-a" @click="bookInfo(scope.row.book_id)">
                    <img alt="" :src="scope.row.book_img" style="height: 60px;width: 60px"/>
                    {{ scope.row.book_name }}
                  </a>
                </template>
              </el-table-column>

              <el-table-column prop="price" label="()" show-overflow-tooltip></el-table-column>

              <el-table-column
                fixed="right" label="" width="120">
                <template slot-scope="scope">
                  <el-input type="number" :min="1" @change="buyNumChange(scope.row)"
                            v-model="scope.row.num"></el-input>
                </template>
              </el-table-column>

              <el-table-column prop="price" label="()" show-overflow-tooltip>
                <template slot-scope="scope">
                    {{scope.row.price*scope.row.num}}
                </template>
              </el-table-column>

              <el-table-column
                fixed="right" label="" width="120">
                <template slot-scope="scope">
                  <el-button type="danger" size="small" @click="deleteRow(scope.row.cart_id)">
                  </el-button>
                </template>
              </el-table-column>

            </el-table>
          </el-col>
        </el-row>

        <!---->
        <el-row>
          <el-col class="pay-nav">
            <el-col :span="4">
              <el-button class="clear-all-cart-btn" type="danger" @click="harvestInfoVisible1 = true">
              </el-button>
              &nbsp;<!--<a href="/"></a>-->
              <el-divider direction="vertical"></el-divider>
            </el-col>
            <el-col :span="8" style="color: #757575;">
              <span style="color: #ff6700;">{{ total }}</span>
              <span style="color: #ff6700;">{{ checkNum }}</span>
            </el-col>
            <el-col :span="8" style="text-align: right;color: #ff6700;">
               <span class="all-money">{{ allMoney }}</span>
            </el-col>
            <el-col :span="4" style="text-align: right;">
              <el-button class="pay-btn" @click="settlementBox"></el-button>
            </el-col>
          </el-col>
        </el-row>
      </el-row>

      <!---->
      <div v-if="carts.length===0">
        <div class="tips-words">
          <i></i>
          <div>
            <h3><a href="/"></a></h3>
            <dl>
              <dt>:</dt>
              <dd>1.</dd>
              <dd>2.</dd>
            </dl>
          </div>
        </div>
        <HomeFooter></HomeFooter>
      </div>

      <!---->
      <el-dialog title="" :visible.sync="harvestInfoVisible" width="30%" @close="harvestInfoClose">
        <el-form :model="settlement" :rules="harvestInfoRrules" ref="harvestInfoRuleForm">
          <el-form-item label="" prop="nick_name">
            <el-input v-model="settlement.nick_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="settlement.tel" autocomplete="off" type="number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="settlement.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="harvestInfoVisible = false"> </el-button>
          <el-button type="primary" @click="receiptInfo"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="harvestInfoVisible1" width="30%" @close="harvestInfoClose1">
        <el-form :model="settlement" :rules="harvestInfoRrules" ref="harvestInfoRuleForm1">
          <el-form-item label="" prop="nick_name">
            <el-input v-model="settlement.nick_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="settlement.tel" autocomplete="off" type="number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="settlement.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="harvestInfoVisible1 = false"> </el-button>
          <el-button type="primary" @click="emptyCart"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 3, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

      <HomeFooter></HomeFooter>
    </div>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  name: "Cart",
  data(){
    //
    var checkMobile = (rule, value, cd) => {
      //
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }
    return{
      //
      user: {},
      //
      queryInfo: {
        user_id: '',
        //
        pagenum: 1,
        //
        pagesize: 5,
      },
      //
      total: 0,
      //
      carts: [],
      //
      allMoney: 0,
      //
      checkNum: 0,
      //
      settlement: {
        //
        multipleSelection: [],
        //
        nick_name: '',
        //
        tel: 0,
        //
        address: '',
      },
      //
      harvestInfoVisible: false,
      //
      harvestInfoRrules: {
        nick_name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 5, message: ' 2  4 ', trigger: 'blur'}
        ],
        tel: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkMobile,
            trigger: 'blur'//
          }
        ],
        address: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {min: 8, max: 18, message: ' 8  18 ', trigger: 'blur'}
        ]
      },
      //
      harvestInfoVisible1: false,
    }
  },
  created() {
    //sessionStorage
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    this.queryInfo.user_id = this.user.user_id
    //id
    this.getCartByUserId()
    //
    this.settlement.address=this.user.address
    this.settlement.tel=this.user.phone
    this.settlement.nick_name=this.user.username
  },
  methods:{
    //id
    getCartByUserId() {
      this.$http.get('/user/getCartByUserId',{params:this.queryInfo}).then(res=>{
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.carts = res.data.data.info.list
        this.total = res.data.data.info.total
        //
        this.allMoney = res.data.data.totalprice.toFixed(2)
      })
    },
    //
    handleSelectionChange(val) {
      this.settlement.multipleSelection = val;
      this.checkNum = this.settlement.multipleSelection.length;
    },
    //id
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //change
    buyNumChange(item) {
      if (item.num < 1) {
        this.$message.error("1")
        item.num = 1
      }

      //
      this.$http.get('/public/getBookDetails?book_id=' + item.book_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        if (res.data.data.book.total < item.num) {
          item.num = res.data.data.book.total
          return this.$message.error(',,' + res.data.data.book.total + '')
        }
        //
        this.$http.get('/user/buyNumChange',{
          params:{
            cart_id: item.cart_id,
            num: item.num
          }
        }).then(res=>{
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.error('')
          }
          this.getCartByUserId()
        })
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCartByUserId()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCartByUserId()
    },
    //id
    async deleteRow(cart_id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.post('/user/deleteRow?cart_id=' + cart_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCartByUserId()
      })
    },
    //
    harvestInfoClose() {
      this.$refs.harvestInfoRuleForm.resetFields()
    },
    //
    harvestInfoClose1() {
      this.$refs.harvestInfoRuleForm1.resetFields()
    },
    //
    settlementBox() {
      if (this.settlement.multipleSelection.length <= 0) {
        return this.$message.info('')
      }
      this.harvestInfoVisible = true
    },
    //
    receiptInfo() {
      this.$refs.harvestInfoRuleForm.validate(valid => {
        if (!valid) return

        var carts = JSON.stringify(this.settlement.multipleSelection)
        this.$http.get('/user/receiptInfo', {
          params: {
            carts: carts,
            nick_name:this.settlement.nick_name,
            tel:this.settlement.tel,
            address:this.settlement.address
          }
        }).then(res => {
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.info('')
          }
          this.$router.go(0)
          this.getCartByUserId()
          this.$message.success('')
          this.harvestInfoVisible = false
        })

      })
    },
    //
    async emptyCart() {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.get('/user/emptyCart', {
        params: {
          user_id: this.queryInfo.user_id,
          nick_name: this.settlement.nick_name,
          tel: this.settlement.tel,
          address: this.settlement.address
        }
      }).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('')
        }
        this.$router.go(0)
        this.getCartByUserId()
        this.$message.success('')
        this.harvestInfoVisible1 = false
      })
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.el-pagination{
  text-align: center;
  margin-top:15px ;
}
.tips-words{
  width: 1200px;
  height: 300px;
  margin: 0 auto;
  margin-top: 10px;
  background-color: #FFF5ED;
}

.tips-words i{
  width: 54px;
  height: 68px;
  margin: 116px 0 116px 60px;
  background-position: -239px -152px;
  display: block;
  float: left;
  padding:0 20px 0 0;
  background: url("https://res.suning.cn/project/search/searchpage_new/css/images/pic.png?20210126") -165px -153px no-repeat;;
}

.tips-words div{
  float: left;
  margin-top: 114px;
}

.tips-words div h3 a{
  color: #28e;
}

.tips-words div h3{
  color: #666666;
}

.tips-words div dl{
  color: #666666;
  font-size: 12px;
}

.tips-words div dl dd em a{
  color: #666666;
  font-style: normal;
}

.cart{
  width: 1200px;
  margin: 10px auto 0;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}


.no-cart > h2 {
  margin: 70px 0 0 0;
  font-size: 36px;
  color: #b0b0b0;
}

.no-cart > a {
  text-decoration: none;
  color: white;
  background-color: #ff6700;
  display: inline-block;
  margin: 30px;
  padding: 20px;
  width: 130px;
  text-align: center;
  border-radius: 20px;
  font-size: 20px;
}

a:hover {
  cursor: pointer;
}

.my-cart a {
  text-decoration: none;
  color: #565656;
  font-size: 20px;
}

.all-money {
  font-size: 25px;
  color: #ff6700;
  font-weight: bold;
}

.pay-btn {
  color: white;
  background-color: #ff6700;
  border-radius: 0;
  border: 0;
  height: 50px;
  width: 160px;
  font-size: 20px;
}

.clear-all-cart-btn {
  border-radius: 0;
  border: 0;
  height: 50px;
  width: 160px;
  font-size: 20px;
}

.pay-nav {
  margin-top: 20px;
  background-color: white;
  height: 50px;
  line-height: 50px;
}

.pay-nav a {
  text-decoration: none;
  color: #757575;
}

.check-item img {
  width: 70px;
  height: 70px;
  margin-top: 10px;
}

.name-a {
  text-decoration: none;
  color: black;
}

.name-a > img {
  float: left;
}

.book-item > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.book-item img {
  display: block;
  margin: 20px auto;
  width: 220px;
  height: 220px;
}


hr {
  width: 350px;
  display: inline-block;
  margin: 0 20px 10px 20px;
}

.main-title > h2 {
  color: #848484;
  text-align: center;
  display: inline-block;
}

.main-title > img {
  display: inline-block;
}
</style>
