<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-row>
        <el-col :span="5">
          <el-button type="primary" @click="oneKeyOrder"></el-button>
        </el-col>
      </el-row>

      <el-table :data="order" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="book_img">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.book_img"></el-image>
          </template>
        </el-table-column>
        <el-table-column label="" prop="book_name"></el-table-column>
        <el-table-column label="" prop="nick_name"></el-table-column>
        <el-table-column label="" prop="tel"></el-table-column>
        <el-table-column label="" prop="address"></el-table-column>
        <el-table-column label="" prop="buy_num"></el-table-column>
        <el-table-column label="" prop="buy_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>
              {{ (scope.row.buy_num * scope.row.buy_price).toFixed(2) }}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.status===0">
              
            </span>
            <span v-if="scope.row.status===1">
              
            </span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-button type="success" @click="updataOrder(scope.row.order_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        //
        pagenum: 1,
        //
        pagesize: 5
      },
      //
      order: [],
      //
      total: 0
    }
  },
  created() {
    //
    this.getOrderList()
  },
  methods: {
    //
    getOrderList() {
      this.$http.get('/admin/getOrderList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.order = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    //
    updataOrder(order_id) {
      this.$http.post('/admin/updataOrder?order_id=' + order_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getOrderList()
      })
    },
    //
    async oneKeyOrder(){
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/admin/oneKeyOrder').then(res=>{
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getOrderList()
      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
