<!---->
<template>
  <div class="login_container">
    <div class="login_box">
      <!---->
      <div class="avatar_box">
        <img src="../../assets/images/logo.png">
      </div>
      <!---->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">

        <!---->
        <el-form-item prop="admin_name">
          <el-input v-model="loginForm.admin_name" prefix-icon="el-icon-user-solid"></el-input>
        </el-form-item>

        <!---->
        <el-form-item prop="admin_password">
          <el-input v-model="loginForm.admin_password" prefix-icon="el-icon-s-goods" type="password" @keyup.enter.native="adminLogin"></el-input>
        </el-form-item>

        <!---->
        <el-form-item class="btns">
          <el-button type="primary" @click="adminLogin"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
          <el-link type="primary" style="margin-left: 10px" @click="goHome"></el-link>
        </el-form-item>

      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      //
      loginForm: {
        admin_name: '',
        admin_password: '',
      },
      //
      loginFormRules: {
        //
        admin_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        //
        admin_password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: ' 6  15 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    //
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },

    //
    adminLogin () {
      //validate
      this.$refs.loginFormRef.validate(async valid => {//validvalidata
        //validtrue
        if (!valid) return

        //
        this.$http.post('/admin/login',this.loginForm,{emulateJSON:true}).then(res=>{
            console.log(res)
          if (res.data.code!==200||res.data.data.loginNo==='loginNo'){
            return this.$message.error('')
          }
          this.$message.success('')
          this.$router.push('/adminHome')
        })
      })
    },
    //
    goHome(){
      this.$router.push('/home')
    }
  }
}
</script>

<style scoped lang="less">
.login_container {
  background-color: #2b4b6b;
  height: 100%;
  background-image:url("../../assets/images/pexels-photo-3704611.jpg");
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0px 0 50px;
  overflow-y: hidden;
  position: relative;
  font-family: 'Montserrat', sans-serif;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}


</style>
