<!---->
<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!--  el-rowel-col-->
      <el-row>
        <el-col :span="5">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  border:stripe-->
      <el-table :data="userlist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-avatar :size="100" :src="scope.row.user_img" v-if="scope.row.user_img!==null"></el-avatar>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>: {{ scope.row.username }}</p>
              <p>: {{ scope.row.gender }}</p>
              <p>: {{ scope.row.age }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.username }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="password" label=""></el-table-column>
        <el-table-column prop="real_name" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="postcode" label=""></el-table-column>
        <el-table-column prop="register_time" label="">
          <template slot-scope="scope">
            {{ scope.row.register_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <!---->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeUserById(scope.row.user_id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--
          @size-change: :page-sizes
          @current-change
          :current-page
          :page-sizes@size-change
          :page-size
          :total
          background:
          -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 3
      },
      //
      userlist: [],
      //
      total: 0,
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    //
    getUserList() {
      this.queryInfo.query=this.queryInfo.query.trim()
      this.$http.get('/admin/getUserList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.userlist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },

    //id
    async removeUserById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.post('/admin/removeUserById?userid=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getUserList()
      })
    },
  }
}
</script>

<style scoped lang="less">
.el-row {
  margin-left: 4px !important;
}

.el-col {
  margin-left: -4px;
}

.el-table {
  margin-top: 10px;
}

.el-card {
  margin-top: 10px;
}

.el-pagination{
  width: 50%;
  margin: 0 auto;
}
</style>
