<!---->
<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-alert title=":" type="success" center show-icon>
      </el-alert>

      <el-upload class="upload-demo" accept="image/jpg" with-credentials
                 :action="uploadURL" multiple name="rotationChartImg"
                 :on-success="fileUpload" :disabled="rotationChartlistLenth">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
      </el-upload>

      <!--  border:stripe-->
      <el-table :data="rotationChartlist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="">
          <template>
            
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-image style="width: 280px; height: 100px"
                      :src="scope.row.img"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="img_name" label=""></el-table-column>
        <el-table-column prop="img" label=""></el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <!---->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeRotationChartById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      rotationChartlist: [],
      //URL
      /*uploadURL: 'http://localhost:8080/bookstore_SSM_Vue/admin/uploadRotationChart',*/
      uploadURL: 'http://localhost:8080/admin/uploadRotationChart',
    }
  },
  created() {
    //
    this.getRotationChartList()
  },
  methods: {
    //
    getRotationChartList() {
      this.$http.get('/admin/getRotationChartList').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.rotationChartlist = res.data.data
      })
    },
    //rotationChartImgaction
    fileUpload(rotationChartImg) {
      console.log(rotationChartImg.filename)
      console.log(rotationChartImg.upload)
      //
      this.$http.post('/admin/addRotationChart', {
          img: rotationChartImg.upload,
          img_name: rotationChartImg.filename
        }
      ).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRotationChartList()
      })
    },
    //
    async removeRotationChartById(id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/admin/removeRotationChartById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRotationChartList()
      })

    }
  },
  //
  computed: {
    rotationChartlistLenth() {
      if (this.rotationChartlist.length == 5) {
        return true;
      }
      return false;
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-table {
  margin-top: 30px;
}

.upload-demo {
  margin-top: 10px;
}
</style>
