<!---->
<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-button type="primary" round @click="oneKeyRead"></el-button>
      <el-table :data="Feedback" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="user_name" label=""></el-table-column>
        <el-table-column prop="feedback_time" label="">
          <template slot-scope="scope">
            {{ scope.row.feedback_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="feedback_info" label=""></el-table-column>
        <el-table-column prop="feedback_read" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.feedback_read===0"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <!---->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeFeedbackById(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="success" icon="el-icon-check" circle size="small"
                         @click="updataReadById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 12]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      Feedback: [],
      //
      queryInfo: {
        //
        pagenum: 1,
        //
        pagesize: 10
      },
      //
      total: 0,
    }
  },
  created() {
    //
    this.getFeedbackInfoList()
  },
  methods: {
    //
    getFeedbackInfoList() {
      this.$http.get('/admin/getFeedbackInfoList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.Feedback = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    async removeFeedbackById(id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.post('/user/removeFeedbackById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getFeedbackInfoList()
      })
    },
    //id
    updataReadById(id) {
      this.$http.post('/admin/updataReadById?id=' + id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getFeedbackInfoList()
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getFeedbackInfoList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getFeedbackInfoList()
    },
    //
    async oneKeyRead() {
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/admin/oneKeyRead').then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getFeedbackInfoList()
      })

    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
