<template>
  <div>
    <!---->
    <div>
      <div id="hd">
        <!---->
        <div id="tools">
          <div class="tools">
            <div class="operate">
              <div class="operate_nav">
                <a @click="goAdminLogin"></a>
              </div>
              <!---->
              <div class="welcome" v-if="isLogin">
                <a class="login" @click="userlogin"></a>
                <a class="register" @click="Register"></a>
              </div>

              <!---->
              <el-dropdown v-else class="el-dropdown">
                  <span class="el-dropdown-link">
                    {{user.username}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <el-avatar size="medium" :src="user.user_img"></el-avatar>
                  </el-dropdown-item>
                  <a class="loginandout" @click="goUserFeedback"></a>
                  <a class="loginandout" @click="goUserInfo"></a>
                  <a class="loginandout" @click="goCollection"></a>
                  <a class="loginandout" @click="logOut" ></a>
                </el-dropdown-menu>
              </el-dropdown>
              <div class="topmenu_left"></div>
            </div>
          </div>
        </div>
        <!---->
        <div class="logo_line_out">
          <div class="logo_line">
            <a class="text_logo" href="/">
              <img src="../../assets/images/logo.png">
            </a>
            <el-input placeholder="" v-model="book_keyword" class="input-with-select"
                      @keyup.enter.native="goBookList">
              <el-button slot="append" icon="el-icon-search" @click="goBookList"></el-button>
            </el-input>
            <el-button type="danger" plain class="custom-btn" @click="goCart"></el-button>
            <el-button type="primary" plain class="custom-btn" @click="goMyOrder"></el-button>
          </div>
        </div>
        <!---->
        <div class="sub">
          <ul>
            <li>
              <a href="#"></a>
            </li>
            <li v-for="item in thirteenCategory" :key="item.category_id">
              <a href="#" @click="goCategoryBookList(item.category_id,item.category_name)">{{ item.category_name }}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <router-view/>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      //
      book_keyword: "",
      //13
      thirteenCategory: [],
      //
      user:{}
    }
  },
  created() {
    //13
    this.getThirteenCategory();
    this.user=JSON.parse(window.sessionStorage.getItem('user'));
  },
  methods: {
    //13
    getThirteenCategory() {
      this.$http.get('/public/getThirteenCategory').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("13")
        }
        this.thirteenCategory = res.data.data
      })
    },
    //
    goBookList() {
      if (this.book_keyword == null || this.book_keyword == '') {
        return this.$message.info('')
      }
      this.book_keyword = this.book_keyword.trim()//
      this.$router.push('/bookList?book_keyword=' + this.book_keyword)
      //
      this.$router.go(0)
    },
    //id
    goCategoryBookList(category_id, category_name) {
      this.$router.push('/categorybooklist?category_id=' + category_id + "&category_name=" + category_name)
      //
      this.$router.go(0)
    },
    //
    userlogin(){
      //
      this.$router.push('/userlogin')
    },
    //
    logOut() {
      console.log(2222)
      //
      window.sessionStorage.clear()
      //
      this.$router.push('/home')
      this.$router.go(0)
    },
    //
    goMyOrder(){
      var user = window.sessionStorage.getItem('user');
      if (user===null){
        return this.$message.info('')
      }
      this.$router.push('/userorder')
    },
    //
    goCart(){
      var user = window.sessionStorage.getItem('user');
      if (user===null){
        return this.$message.info('')
      }
      this.$router.push('/cart')
    },
    //
    Register(){
      this.$router.push('/userregister')
    },
    //
    goUserInfo(){
      this.$router.push('/userinfo')
    },
    //
    goUserFeedback(){
      this.$router.push('/userfeedback')
    },
    //
    goCollection(){
      this.$router.push('/usercollection')
    },
    //
    goAdminLogin(){
      this.$router.push('/adminLogin')
    }
  },
  computed: {
    //
    isLogin() {
      var user = window.sessionStorage.getItem('user');
      if (user !== null) {
        return false
      }
      return true
    }
  }
}
</script>

<style scoped lang="less">
.el-dropdown{
  cursor: pointer;
}
#hd {
  font: 12px 'Hiragino Sans GB', "simsun", "Arial";
}

#hd .tools {
  width: 100%;
  height: 35px;
  background-color: #F9F9F9;
  border-bottom: 1px solid #f2f2f2;
}

.tools .operate {
  width: 1200px;
  height: 35px;
  margin: 0 auto;
  background-color: #f9f9f9;
}

.tools .operate_nav {
  float: right;
  padding-top: 2px;
  padding-left: 15px;
  width: 80px;
  height: 29px;
}

.operate_nav a {
  display: inline-block;
  width: 75px;
  height: 22px;
  padding-top: 5px;
  line-height: 22px;
  text-align: center;
}

.tools .welcome {
  float: right;
  padding-top: 2px;
  width: 160px;
  height: 29px;
}

.tools .welcome a:hover{
  color: #FF8236;
}

.welcome .login, .register {
  display: inline-block;
  width: 70px;
  height: 22px;
  float: left;
  padding-top: 5px;
  line-height: 22px;
  text-align: center;
}

.tools .operate .el-dropdown{
  float: right;
  padding-top: 11px;
  margin-right: 10px;
}

.loginandout{
  font-size: 14px;
  display: block;
  text-align: center;
  margin-top: 5px;
}

.loginandout:hover{
  color: #FF8236;
}

.operate .topmenu_left {
  line-height: 35px;
}

.logo_line_out .logo_line {
  width: 1200px;
  height: 80px;
  margin: 0 auto;
  /*background-color: red;*/
  position: relative;
}

.text_logo {
  float: left;
  width: 218.18px;
  height: 80px;

}

.text_logo img {
  height: 80px;
  width: 218.18px;
}


.logo_line .input-with-select {
  position: absolute;
  width: 394px;
  left: 395px;
  margin-top: 20px;
}

.logo_line .custom-btn {
  margin-top: 20px;
  float: right;
  width: 100px;
  margin-right: 5px;
}

.sub {
  background: #f9f9f9;
  float: none;
  width: auto;
}

.sub ul {
  width: 1200px;
  margin: 0 auto;
  height: 31px;
  overflow: hidden;
  line-height: 31px;
  padding-left: 0px;

}

.sub ul li {
  display: inline-block;
  float: left;
  padding: 0 15px;
  white-space: nowrap;
}

.sub ul li a {
  color: #646464;
  text-decoration: none;
}

.sub ul li a:hover{
  color: #FFB588;
}
</style>
