<!---->
<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!--  el-rowel-col-->
      <el-row :gutter="20">
        <el-col :span="5">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getCategoryList">
            <el-button slot="append" icon="el-icon-search" @click="getCategoryList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-input placeholder="" v-model="addCategoryName" class="addcategoryinput">
          </el-input>
          <el-button type="primary" @click="addCategory"></el-button>
        </el-col>
      </el-row>
      <el-table :data="categoryList" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="category_name" label=""></el-table-column>
        <el-table-column label="" width="250px">
          <template slot-scope="scope">
            <!---->
            <el-button type="success" @click="queryCategoryById(scope.row.category_id)"></el-button>
            <!---->
            <el-button type="danger" @click="removeCategory(scope.row.category_id)"></el-button>

          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>


      <!---->
      <el-dialog title="" :visible.sync="categoryVisible" width="30%" @close="categoryClosed">
        <el-form :model="category" :rules="categoryFormRules" ref="categoryFormRef" label-width="100px">
          <el-form-item label="id">
            <el-input v-model="category.category_id" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="category_name">
            <el-input v-model="category.category_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="categoryVisible = false"> </el-button>
          <el-button type="primary" @click="updataCategory"> </el-button>
        </span>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      categoryList: [],
      //
      queryInfo: {
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 10
      },
      //
      total: 0,
      //
      addCategoryName: '',
      //
      categoryVisible: false,
      //
      category: {},
      //
      categoryFormRules: {
        category_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 6,
            message: '2~6',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //
    this.getCategoryList()
  },
  methods: {
    //
    getCategoryList() {
      this.queryInfo.query = this.queryInfo.query.trim()
      this.$http.get('/admin/getCategoryList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.categoryList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCategoryList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCategoryList()
    },
    //
    addCategory() {
      if (this.addCategoryName === '') {
        return this.$message.info('')
      }
      this.$http.post('/admin/addCategory?category_name=' + this.addCategoryName).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addCategoryName = ''
        this.getCategoryList()
      })
    },
    //
    categoryClosed() {
      this.$refs.categoryFormRef.resetFields()
    },
    //id
    queryCategoryById(id) {
      this.$http.get('/admin/queryCategoryById?id=' + id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.category = res.data.data
        this.categoryVisible = true
      })
    },
    //
    updataCategory() {
      this.$http.post('/admin/updataCategory', this.category, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.info('')
        this.categoryVisible = false
        this.getCategoryList()
      })
    },
    //
    async removeCategory(id) {
      const confirmResult = await this.$confirm('?!', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)
      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/admin/removeCategory?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data === 'removeNo2') {
          return this.$message.error('')
        }
        if (res.data.data === 'removeNo1') {
          this.$message.info(',')
        }
        if (res.data.data === 'removeYes') {
          this.$message.success('')
          this.getCategoryList()
        }

      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.addcategoryinput {
  width: 60%;
}

.el-table {
  margin-top: 10px;
}
</style>
