<template>
  <div>
    <div class="home clearfix">
      <a href="/"></a>
      <a @click="previousPage"></a>
    </div>

    <!---->
    <div class="info_box clearfix">
      <!---->
      <div class="info_img_box">
        <div class="info_img_small">
          <img :src="book.book_img">
        </div>
      </div>
      <!---->
      <div class="info_book">
        <h3 class="info_title">
          {{ book.book_name }}
        </h3>

        <div class="info_price">
          <p>
            {{ book.author }}
          </p>
          <p>
            {{ category.category_name }}
          </p>
          <p>
            {{ book.book_man }}
          </p>
          <p>
            {{ book.total }}
          </p>
          <p>
            {{ book.add_time|dateFormat1 }}
          </p>
          <p>
            {{ book.sales }}
          </p>
          <p class="introduction">
            {{ book.introduction }}
          </p>
          <p class="price-p">
             {{ book.price }}
          </p>
        </div>

        <el-form inline>
          <!---->
          <el-form-item>
            <el-input placeholder="" v-model="harvestInfo.buy_num" type="number" :min="1" :max="book.total"></el-input>
          </el-form-item>
          <el-form-item v-if="book.total>0">
            <el-button type="danger" @click="addShoppingCart"></el-button>
            <el-button type="primary" @click="harvestInDialog"></el-button>
            <el-button type="success" @click="collection"></el-button>
          </el-form-item>

          <!---->
          <el-form-item v-if="book.total<=0">
            <el-button type="danger" disabled></el-button>
            <el-button type="primary" disabled>
              
            </el-button>
            <el-button type="success" disabled @click="collection"></el-button>
            <el-button type="primary" round disabled></el-button>
          </el-form-item>
        </el-form>

      </div>
    </div>

    <div class="bookcomment">
      <div class="commenttitle">
        <h3></h3>
      </div>

      <div class="textarea clearfix">
        <el-input type="textarea" placeholder=""
                  v-model="textarea" maxlength="30" show-word-limit :disabled="islogin1">
        </el-input>
        <el-button type="primary" @click="addComment"></el-button>
      </div>
      <!---->
      <!--<template>
        <el-table :data="bookComment" style="width: 100%">
          <el-table-column prop="user_img" label="" width="180">
            <template slot-scope="scope">
              <el-avatar :size="50" :src="scope.row.user_img"></el-avatar>
            </template>
          </el-table-column>
          <el-table-column prop="user_name" label="" width="180"></el-table-column>
          <el-table-column prop="comment_time" label="" width="180" sortable>
            <template slot-scope="scope">
                <span>
                  {{ scope.row.comment_time|dateFormat1 }}
                </span>
            </template>
          </el-table-column>
          <el-table-column prop="comment_info" label=""></el-table-column>
          <el-table-column label="" v-if="islogin" width="180">
            <template slot-scope="scope">
              &lt;!&ndash;&ndash;&gt;
              <el-button v-if="scope.row.user_id===user.user_id"
                         type="danger" icon="el-icon-delete" circle
                         @click="deleteCommentById(scope.row.id)"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="" v-else width="180">
          </el-table-column>
        </el-table>
      </template>-->

      <div class="commentarea" v-for="(item,i) in bookComment" :key="item.id">
        <div class="comment-hd clearfix">
          <img :src="item.user_img">
          <div class="commentinfo">
            {{ item.user_name }}<br>
            {{i+1}}   {{ item.comment_time|dateFormat1 }}
          </div>
          <div class="commentoperation" v-if="islogin">
            <el-button v-if="item.user_id===user.user_id"
                       type="danger" icon="el-icon-delete" circle
                       @click="deleteCommentById(item.id)"></el-button>
          </div>
        </div>
        <div class="comment-bd">
          <p>{{item.comment_info}}</p>
        </div>
      </div>

      <!---->
      <el-pagination style="text-align: center"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="commentInfo.pagenum"
                     :page-sizes="[1, 2, 4, 5]"
                     :page-size="commentInfo.pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="harvestInfoVisible" width="30%" @close="harvestInfoClose">
      <el-form :model="harvestInfo" :rules="harvestInfoRrules" ref="harvestInfoRuleForm">
        <el-form-item label="" prop="nick_name">
          <el-input v-model="harvestInfo.nick_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="harvestInfo.tel" autocomplete="off" type="number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="harvestInfo.address" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="harvestInfoVisible = false"> </el-button>
        <el-button type="primary" @click="buyNow"> </el-button>
      </div>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>

</template>

<script>
import HomeFooter from "./HomeFooter";
import BookList from "./BookList";

export default {
  name: "BookInfo",
  data() {
    //
    var checkMobile = (rule, value, cd) => {
      //
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }
    return {
      //
      book: {},
      //
      category: {},
      //id
      book_id: '',
      //
      harvestInfo: {
        //
        nick_name: '',
        //
        tel: 0,
        //
        address: '',
        //id
        user_id: '',
        //id
        book_id: '',
        //
        book_name: '',
        //
        buy_price: '',
        //
        book_img: '',
        //
        buy_num: 1,
      },
      //
      textarea: '',
      //
      bookComment: [],
      //id
      commentInfo: {
        book_id: '',
        //
        pagenum: 1,
        //
        pagesize: 5,
      },
      //
      total: 0,
      //
      addCommentForm: {
        user_id: '',
        user_name: '',
        comment_info: '',
        book_id: '',
        book_name: '',
        user_img: ''
      },
      //
      userCollectionForm: {
        user_id: '',
        book_id: '',
        book_name: '',
        book_img: ''
      },
      //
      user: {},
      //
      harvestInfoVisible: false,
      //
      harvestInfoRrules: {
        nick_name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 5, message: ' 2  4 ', trigger: 'blur'}
        ],
        tel: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkMobile,
            trigger: 'blur'//
          }
        ],
        address: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {min: 8, max: 18, message: ' 8  18 ', trigger: 'blur'}
        ]
      },
    }
  },
  created() {
    //id
    this.getBookDetails(this.$route.query.book_id)
    //id
    this.book_id = this.$route.query.book_id
    //id
    this.getCommentList()
    //sessionStorage
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //
    this.userReceivingInfo()

  },
  methods: {
    //
    userReceivingInfo() {
      if (this.user == '' || this.user == null) {
        return
      }
      this.harvestInfo.address = this.user.address
      this.harvestInfo.tel = this.user.phone
      this.harvestInfo.nick_name = this.user.username
    },

    //id
    getBookDetails(book_id) {
      if (this.$route.query.book_id == '' || this.$route.query.book_id == undefined) {
        this.$router.push('/')
      }

      console.log(book_id)
      this.$http.get('/public/getBookDetails', {params: {book_id}}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }

        this.book = res.data.data.book
        this.category = res.data.data.category
      })
    },
    //id
    getCommentList() {
      //id
      this.commentInfo.book_id = this.$route.query.book_id
      this.$http.get('/user/getCommentList', {params: this.commentInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.bookComment = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.commentInfo.pagesize = newSize
      this.getCommentList()
    },
    //
    handleCurrentChange(newPage) {
      this.commentInfo.pagenum = newPage
      this.getCommentList()
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
    //
    addComment() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return this.$message.info('')
      }

      if (this.textarea.trim() === '') {
        return this.$message.info('')
      }
      this.addCommentForm.user_id = this.user.user_id
      this.addCommentForm.user_name = this.user.username
      this.addCommentForm.comment_info = this.textarea.trim()
      this.addCommentForm.book_id = this.book_id
      this.addCommentForm.book_name = this.book.book_name
      this.addCommentForm.user_img = this.user.user_img

      this.$http.post('/user/addComment', this.addCommentForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('')
        }
        this.$message.success('')
        this.textarea = ''
        this.getCommentList()
      })
    },
    //id
    async deleteCommentById(id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/user/deleteCommentById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCommentList()
      })

    },
    //
    collection() {
      if (this.user == null || this.user == '') {
        return this.$message.info('')
      }
      this.userCollectionForm.user_id = this.user.user_id
      this.userCollectionForm.book_id = this.book_id
      this.userCollectionForm.book_name = this.book.book_name
      this.userCollectionForm.book_img = this.book.book_img

      this.$http.post('/user/collectionBook', this.userCollectionForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        if (res.data.data.collectionNo === "collectionNo") {
          return this.$message.error('')
        }
        if (res.data.data.collectionYes === "collectionYes") {
          return this.$message.success('')
        }
      })
    },
    //
    harvestInDialog() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return this.$message.info('')
      }
      //
      this.harvestInfoVisible = true
    },
    //
    buyNow() {
      this.harvestInfo.user_id = this.user.user_id
      this.harvestInfo.book_id = this.book.book_id
      this.harvestInfo.book_name = this.book.book_name
      this.harvestInfo.buy_price = this.book.price
      this.harvestInfo.book_img = this.book.book_img

      if (this.harvestInfo.buy_num === '' || this.harvestInfo.buy_num <= 0) {
        return this.$message.error(',')
      }

      if (this.harvestInfo.buy_num > this.book.total) {
        return this.$message.info('')
      }

      this.$refs.harvestInfoRuleForm.validate(async valid => {
        if (!valid) return
        await this.$http.post('/user/addShoppingCart', this.harvestInfo, {emulateJSON: true}).then(res => {
          if (res.data.code !== 200 || res.data.data !== 1) {
            return this.$message.error('')
          }
          this.$message.success('')
          this.harvestInfo.buy_num = 1
          this.harvestInfoVisible = false
          //
          this.getBookDetails(this.book_id)
        })

      })
    },
    //
    harvestInfoClose() {
      this.$refs.harvestInfoRuleForm.resetFields()
    },
    //
    addShoppingCart() {
      var user = window.sessionStorage.getItem('user');
      if (user === null) {
        return this.$message.info('')
      }

      /*if (this.harvestInfo.buy_num === '' || this.harvestInfo.buy_num <= 0) {
        return this.$message.error(',')
      }*/

      if (this.harvestInfo.buy_num > this.book.total) {
        return this.$message.info('')
      }

      this.$http.post('/user/addCart', {
        user_id: this.user.user_id,
        book_id: this.book_id,
        book_name: this.book.book_name,
        book_img: this.book.book_img,
        price: this.book.price,
        num: this.harvestInfo.buy_num,
      }).then(res => {
        if (res.data.code !== 200 || res.data.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.harvestInfo.buy_num = 1
      })
    }
  },
  computed: {
    //,
    islogin() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item !== null) {
        return true
      }
      return false
    },
    //
    islogin1() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return true
      }
      return false
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.comment-bd {
  width: 800px;
  margin: 7px 0 0 86px;
  /*1.*/
  white-space:nowrap; (normal)
  /*2.*/
overflow:hidden;
  /*3.*/
  text-overflow:ellipsis;
}

.comment-bd p{
  font-size: 16px;
  color: #646464;
}

.commentoperation{
  line-height: 50px;
  position: absolute;
  top: 50%;
