  padding-right: 30px;
  color: #646464;
}

.el-form {
  margin-top: 18px;
}

.info_price .price-p {
  color: #ff0000;
  font-size: 25px;
  font-weight: 700;
}

.info_price p {
  color: #555555;
  margin: 10px 0 0 10px;
}


.introduction {
  height: 84px;
  overflow: auto;
}

.info_price {
  margin-top: 10px;
  height: 315px;
  background-color: #FFF8F3;
  width: 100%;
}

.info_title {
  color: #181818;
  font-size: 25px;
  width: 780px;
  white-space: nowrap; /**/
  overflow: hidden; /**/
  text-overflow: ellipsis; /*(...)overflow:hidden;*/
}

.info_box {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
  position: relative;
}

.info_img_box {
  float: left;
  position: relative;
  border: 1px solid #fff;
  margin: 20px;
}

.info_img_small {
  position: relative;
  width: 350px;
  height: 410px;
}

.info_img_small img {
  width: 100%;
  height: 100%;
}

.info_book {
  margin-left: 415px;
  padding-top: 15px;
  width: 65%;
  height: auto;
}
</style>
