<template>
  <div class="userinfo">
    <!---->
    <div class="home clearfix">
      <a href="/"></a>
      <a @click="previousPage"></a>
    </div>

    <!--<el-descriptions title="">
      <el-descriptions-item label="">
        <el-avatar :size="100" :src="user.user_img"></el-avatar>
      </el-descriptions-item>
      <el-descriptions-item label="">{{ user.username }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.password }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.real_name }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.gender }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.age }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.phone }}</el-descriptions-item>
      <el-descriptions-item label="">
        <el-tag size="small">{{ user.email }}</el-tag>
      </el-descriptions-item>
      <el-descriptions-item label="">{{ user.address }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.postcode }}</el-descriptions-item>
      <el-descriptions-item label="">{{ user.register_time|dateFormat1 }}</el-descriptions-item>
      <el-descriptions-item label="">
        <el-upload class="upload-demo" drag accept="image/jpg" with-credentials
                   :action="uploadURL" multiple name="userImg" :on-success="fileUpload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
        </el-upload>
      </el-descriptions-item>
    </el-descriptions>-->
    <el-descriptions class="margin-top" title="" :column="3" border>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-user"></i>
          
        </template>
        {{ user.username }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-unlock"></i>
          
        </template>
        {{ user.password }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-mobile-phone"></i>
          
        </template>
        {{ user.phone }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-location-outline"></i>
          
        </template>
        {{ user.address }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          
        </template>
        <el-tag size="small">{{ user.real_name }}</el-tag>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-s-custom"></i>
          
        </template>
        {{ user.gender }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-star-off"></i>
          
        </template>
        {{ user.age }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-s-cooperation"></i>
          
        </template>
        {{user.email}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          
        </template>
        {{ user.postcode }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-date"></i>
          
        </template>
        {{ user.register_time|dateFormat1 }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-picture-outline"></i>
          
        </template>
        <el-avatar :size="100" :src="user.user_img"></el-avatar>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-picture-outline-round"></i>
          
        </template>
        <el-upload class="upload-demo" drag accept="image/jpg" with-credentials
                   :action="uploadURL" multiple name="userImg" :on-success="fileUpload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
        </el-upload>
      </el-descriptions-item>

    </el-descriptions>

    <div class="tooltip">
      <el-tooltip content="" placement="top" :enterable="false">
        <el-button type="primary" icon="el-icon-edit" circle @click="editDialogVisibleBox"></el-button>
      </el-tooltip>
      <el-tooltip content="" placement="top" :enterable="false">
        <el-button type="danger" icon="el-icon-delete" circle @click="cancelAccount"></el-button>
      </el-tooltip>
      <el-tooltip content="" placement="top" :enterable="false">
        <el-button type="success" icon="el-icon-star-off" circle @click="goCollection"></el-button>
      </el-tooltip>
      <el-tooltip content="" placement="top" :enterable="false">
        <el-button type="info" icon="el-icon-message" circle @click="goFeedback"></el-button>
      </el-tooltip>
    </div>



    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editDialogClosed">
      <el-form :model="user1" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="user1.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="user1.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="real_name">
          <el-input v-model="user1.real_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="user1.age" type="number" min="1" max="100"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="user1.gender">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="user1.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="user1.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="user1.address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="postcode">
          <el-input v-model="user1.postcode"></el-input>
        </el-form-item>
        <el-form-item label="">
          {{ user.register_time|dateFormat1 }}
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUserInfo"> </el-button>
  </span>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    //
    var checkEmail = (rule, value, cd) => {
      //
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }

    //
    var checkMobile = (rule, value, cd) => {
      //
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }
    return {
      //
      user: {},
      //
      user1: {},
      //URL
      uploadURL: 'http://localhost:8080/user/uploadUserTx',
      //
      editDialogVisible: false,
      //
      editFormRules: {
        password: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        real_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 8,
            message: '2~8',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkMobile,
            trigger: 'blur'//
          }
        ],
        email: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkEmail,
            trigger: 'blur'//
          }
        ],
        address: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        postcode: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 5,
            max: 8,
            message: '5~8',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //sessionStorage
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //id
    this.userInfo()
  },
  methods: {
    //userImgaction
    fileUpload(userImg) {
      this.$http.get('/user/updataUserTx', {
        params: {
          user_id: this.user.user_id,
          user_img: userImg
        }
      }).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.userInfo()
      })
    },
    //id
    userInfo() {
      this.$http.get('/user/userInfo?user_id=' + this.user.user_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }

        this.user = res.data.data
        //sessionStorage
        window.sessionStorage.setItem('user', JSON.stringify(this.user))
      })
    },
    //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //
    editUserInfo() {
      //
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return this.$message.error('')
        /*//URLSearchParams
        let param = new URLSearchParams();
        //JSON
        param.append('user', JSON.stringify(this.user1))*/

        var user=JSON.stringify(this.user1)
        this.$http.get('/user/editUserInfo', {params: {user:user}}).then(res => {
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.error('')
          }
          this.$message.success('')
          this.userInfo()
          this.editDialogVisible = false
        })
      })
    },
    //
    editDialogVisibleBox() {
      this.$http.get('/user/userInfo?user_id=' + this.user.user_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.user1 = res.data.data
        this.editDialogVisible = true
      })
    },
    //id()
    async cancelAccount() {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
    },
    //
    goCollection(){
      this.$router.push('/usercollection')
    },
    //
    goFeedback(){
      this.$router.push('/userfeedback')
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.tooltip{
  margin-top: 10px;
}

.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}

.userinfo{
  width: 1200px;
  margin: 10px auto 0;
}
</style>
