<template>
  <div id="nav">
    <vue-particles
      color="#555"
      :particleOpacity="0.7"
      :particlesNumber="150"
      shapeType="circle"
      :particleSize="4"
      linesColor="#555"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="false"
      clickMode="push"
      class="lizi"
    />

    <div id="login-container">
      <div style="text-align: center;height: 50px;margin-left: 11%">
        
      </div>
      <el-form status-icon :model="registerForm" :rules="registerFormRules" ref="registerFormRef" label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="" prop="username">
          <el-input type="text" autocomplete="off" v-model="registerForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="registerForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="real_name">
          <el-input autocomplete="off" v-model="registerForm.real_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="registerForm.gender">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input autocomplete="off" v-model.number="registerForm.age" min="1" max="100"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input autocomplete="off" v-model="registerForm.phone" type="number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input autocomplete="off" v-model="registerForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input autocomplete="off" v-model="registerForm.address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="postcode">
          <el-input autocomplete="off" v-model="registerForm.postcode" type="number"></el-input>
        </el-form-item>
        <el-form-item label="">
          {{ registerForm.register_time|dateFormat1 }}
        </el-form-item>

        <el-form-item style="margin-top: -20px">
          <el-button type="primary" @click="register"></el-button>
          <el-button @click="resetRegisterForm"></el-button>
          <el-link type="primary" @click="returnhome" style="margin-left: 10px"></el-link>
          <el-link type="primary" style="margin-left: 10px" @click="goLogin"></el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    //
    var checkEmail = (rule, value, cd) => {
      //
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }

    //
    var checkMobile = (rule, value, cd) => {
      //
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //
        return cd()
      }
      cd(new Error(''))
    }
    return {
      //
      registerForm: {
        username: '',
        password: '',
        real_name: '',
        gender: '',
        age: '',
        phone: '',
        email: '',
        address: '',
        postcode: '',
        register_time: '',
      },
      //
      registerFormRules: {
        username: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 1,
            max: 8,
            message: '1~8',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        real_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 8,
            message: '2~8',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkMobile,
            trigger: 'blur'//
          }
        ],
        email: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            validator: checkEmail,
            trigger: 'blur'//
          }
        ],
        address: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        postcode: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 5,
            max: 8,
            message: '5~8',
            trigger: 'blur'
          }
        ],
        //
        age: [
          {required: true, message: ''},
          {type: 'number', message: ''}]
      },


    }
  },
  created() {
    //
    this.registerForm.register_time = new Date()
  },
  methods: {
    //
    register() {
      this.$refs.registerFormRef.validate(valid=>{
        if (!valid) return

        this.$http.get('/user/registerUser',{params:this.registerForm}).then(res=>{
          if (res.data.data.registerNo==="registerNo"){
            return this.$message.error('')
          }
          if (res.data.data.registerYes==="registerYes"){
            this.$router.push('/home')
            return this.$message.success('')
          }
        })
      })
    },
    //
    resetRegisterForm() {
      this.$refs.registerFormRef.resetFields()
    },
    //
    returnhome() {
      this.$router.push('/home')
    },
    //
    goLogin() {
      this.$router.push('/userlogin')
    }
  }
}
</script>

<style scoped>
body {
  margin: 0;
}

#login-container {
  width: 400px;
  height: 710px;
  background: #e5e9f2;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -220px;
  margin-top: -375px;
  border-radius: 5px;
  padding-top: 40px;
  padding-right: 40px;
}

body {
  width: 100%;
  background-color: rgb(239, 239, 239);
}

.lizi {
  position: fixed;
  top: 0;
  width: 100%;
}

#particles-js {
  width: 100%;
}
</style>
