<template>
  <div class="userFeedback">
    <!---->
    <div class="home clearfix">
      <a href="/"></a>
      <a @click="previousPage"></a>
    </div>
    <el-card>
      <h3>{{ user.username }}</h3>
      <el-row :gutter="10" style="margin-top: 10px;">
        <el-col :span="8">
          <el-input type="textarea" placeholder=""
                    v-model="textarea" maxlength="30"
                    show-word-limit></el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" round @click="addFeedback" style="margin-top: 6px"></el-button>
        </el-col>
      </el-row>
      <el-table :data="feedbackList" style="width: 100%">
        <el-table-column type="index" width="100"></el-table-column>
        <el-table-column prop="feedback_info" label="" width="300"></el-table-column>
        <el-table-column prop="feedback_time" label="" width="300">
          <template slot-scope="scope">
            {{ scope.row.feedback_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="feedback_read" label="" width="400">
          <template slot-scope="scope">
            <span v-if="scope.row.feedback_read===0"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle
                         @click="removeFeedbackById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    return {
      //
      user: {},
      //
      queryInfo: {
        //id
        user_id: '',
        //
        pagenum: 1,
        //
        pagesize: 4,
      },
      //
      feedbackList: [],
      //
      total: 0,
      //
      textarea: '',
      //8
      randomBookList: [],
    }
  },
  created() {
    //sessionStorage
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //
    this.getFeedbackList()
  },
  methods: {
    //
    getFeedbackList() {
      this.queryInfo.user_id = this.user.user_id
      this.$http.get('/user/getFeedbackList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.feedbackList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getFeedbackList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getFeedbackList()
    },
    //
    addFeedback() {
      if (this.textarea === '' || this.textarea.trim() === '') {
        return this.$message.info('')
      }
      this.$http.post('/user/addFeedback', {
        user_name: this.user.username,
        feedback_info: this.textarea.trim(),
        user_id: this.user.user_id
      }, {emulateJSON: true}).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.textarea = ''
        this.getFeedbackList();
      })
    },
    //
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //
    async removeFeedbackById(id) {
      const confirmResult = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err => err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.post('/user/removeFeedbackById?id=' + id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getFeedbackList()
      })
    },
    //
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}
.userFeedback{
  width: 1260px;
  margin: 0 auto;
  margin-top: 10px;
}
.el-card {
  width: 100%;
  margin: 10px auto 0;
}

.name-a {
  cursor: pointer
}

.el-pagination {
  text-align: center;
}

.main-item {
  width: 1200px;
  margin: 0 auto;
  /*background-color: deepskyblue;*/
  height: 200px;
}

.main-title {
  text-align: center
}

.book-item {
  width: 240px;
  height: 350px;
  background-color: white;
  margin: 0 20px 20px 20px;
  border: 1px none;
  text-align: center;
}

.book-item img {
  display: block;
  margin: 20px auto;
  width: 220px;
  height: 220px;
}

.book-item:hover {
  box-shadow: 10px 10px 10px 5px #e1e1e1;
}

.book-item > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}
</style>
