<template>
    <div class="morebookcategory clearfix">
      <div class="el-cascader">
        <el-cascader
          :options="bookCategoryTwo" filterable
          :props="cascaderProps" v-model="selectedKeys"
          @change="parentCateChanged"></el-cascader>
      </div>

      <ul class="clearfix bookCategoryOne">
        <li v-for="item in bookCategoryOne" :key="item.category_id" @click="categoryBook(item.category_id,item.category_name)">
          <a>
            {{item.category_name}}
          </a>
        </li>
      </ul>
      <!---->
      <div class="el-pagination-one">
        <el-pagination
          @size-change="categoryHandleSizeChange"
          @current-change="categoryHandleCurrentChange"
          :current-page="categoryQueryInfo.pagenum"
          :page-sizes="[1, 3, 5, 12]"
          :page-size="categoryQueryInfo.pagesize"
          layout="total, prev, pager, next, jumper"
          :total="categoryTotal">
        </el-pagination>
      </div>

      <!---->
      <div class="el-breadcrumb">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">()</el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item>{{categoryName}}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <!---->
      <ul class="clearfix book">
        <li v-for="item in booklist" :key="item.book_id" @click="goBookInfo(item.book_id)">
          <img :src="item.book_img">
          <h4>{{item.book_name}}</h4>
          <div class="info">
            <span>
               {{item.price}}
            </span>
          </div>
        </li>
      </ul>

      <!---->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="bookQueryInfo.pagenum"
          :page-sizes="[20,30,40,50,60]"
          :page-size="bookQueryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="bookTotal">
        </el-pagination>
      </div>

      <HomeFooter></HomeFooter>
    </div>
</template>

<script>
import HomeFooter from "./HomeFooter";

export default {
  name: "MoreBookCategory",
  data(){
    return{
      //
      categoryQueryInfo: {
        //
        pagenum: 1,
        //
        pagesize: 45
      },
      //
      bookCategoryOne: [],
      //
      categoryTotal:0,
      //
      bookCategoryTwo: [],
      //
      cascaderProps: {
        //
        value: 'category_id',
        //
        label: 'category_name',
      },
      //Id
      selectedKeys: [],
      //
      booklist:[],
      //
      bookTotal:0,
      //
      categoryName:'',
      //id
      categoryId:'',
      //
      bookQueryInfo: {
        //id
        categoryId: 0,
        //
        pagenum: 1,
        //
        pagesize: 20
      },
      //
      judge:'0'
    }
  },
  created() {
    //1
    this.getBookCategoryListOne()
    //2
    this.getBookCategoryListTwo()
  },
  methods:{
    //1
    getBookCategoryListOne(){
      this.$http.get('/public/getBookCategoryListOne',{params:this.categoryQueryInfo}).then(res=>{
        if (res.data.code!==200){
          return this.$message.error('')
        }
        this.bookCategoryOne=res.data.data.list
        this.categoryTotal=res.data.data.total
      })
    },
    //
    parentCateChanged() {
      this.judge='0'
      if (this.selectedKeys.length > 0) {
        this.bookQueryInfo.categoryId = this.selectedKeys[this.selectedKeys.length - 1]
        this.getCategoryById(this.selectedKeys[this.selectedKeys.length - 1])
        this.$http.get('/public/bookcategoryById', {params: this.bookQueryInfo}).then(res => {
          if (res.data.code !== 200) {
            return this.$message.error('')
          }
          this.booklist = res.data.data.list
          this.bookTotal = res.data.data.total;
        })
      }
    },
    //id
    getCategoryById(category_id){
      this.$http.get('/public/getCategoryById?category_id='+category_id).then(res=>{
          if (res.data.code!=200){
            return this.$message.error('')
          }
          this.categoryName=res.data.data.category_name
      })
    },
    //
    categoryHandleSizeChange(newSize) {
      this.categoryQueryInfo.pagesize=newSize
      this.getBookCategoryListOne()
    },
    //
    categoryHandleCurrentChange(newPage){
      this.categoryQueryInfo.pagenum=newPage
      this.getBookCategoryListOne()
    },
    //2
    getBookCategoryListTwo(){
      this.$http.get('/public/getBookCategoryListTwo').then(res=>{
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.bookCategoryTwo = res.data.data
        this.selectedKeys.push(this.bookCategoryTwo[0].category_id)
        this.parentCateChanged()
      })
    },
    //id
    goBookInfo(book_id){
      this.$router.push('/bookinfo?book_id='+book_id)
    },
    //
    handleSizeChange(newSize) {
      if (this.judge==='0'){
        this.bookQueryInfo.pagesize = newSize
        this.parentCateChanged()
      }else {
        this.bookQueryInfo.pagesize = newSize
        this.categoryBook(this.categoryId,this.categoryName)
      }

    },
    //
    handleCurrentChange(newPage) {
      if (this.judge==='0'){
        this.bookQueryInfo.pagenum = newPage
        this.parentCateChanged()
      }else {
        this.bookQueryInfo.pagenum = newPage
        this.categoryBook(this.categoryId,this.categoryName)
      }

    },
    //
    categoryBook(category_id,category_name){
      this.judge='1'
      this.categoryName=category_name
      this.bookQueryInfo.categoryId = category_id
      this.categoryId=category_id
      this.$http.get('/public/bookcategoryById', {params: this.bookQueryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.booklist = res.data.data.list
        this.bookTotal = res.data.data.total;
      })
    }

  },
  components:{
    HomeFooter
  }
}
</script>

<style scoped>
.morebookcategory{
  width: 1200px;
  margin: 10px auto 0;
}

.morebookcategory .bookCategoryOne li{
  float: left;
  padding:0 15px;
  margin: 10px 10px;
}

.morebookcategory .bookCategoryOne li a{
  color: #666666;
  font-size: 14px;
}

.morebookcategory .el-pagination-one{
  width: 50%;
  margin: 0 auto;
  margin-top: 10px;
}

.morebookcategory .el-breadcrumb{
  margin-top: 10px;
}

.morebookcategory .book{
  width: 1200px;
  margin-top: 10px;
}

.morebookcategory .book li{
  float: left;
  width: 225px;
  height: 300px;
  background-color: #FFFFFF;
  margin-right: 8px;
  margin-bottom: 15px;
  border: 1px solid #f3f3f3;
}

.morebookcategory .book li:hover{
  z-index: 2; /**/
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1); /**/
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0); /**/
  transform: translate3d(0, -2px, 0);
}

.morebookcategory .book li>img{
  width: 150px;
  height: 150px;
  margin: 20px 0 10px 35px;
}

.morebookcategory .book li h4{
  height: 46px;
  padding: 10px 20px 0;
  font-size: 12px;
  font-weight: 400;
  /**/
  overflow: hidden;
  /**/
  text-overflow: ellipsis;
  /*  */
  display: -webkit-box;
  /*  */
  -webkit-line-clamp: 3;
  /*  */
  -webkit-box-orient: vertical;
}

.morebookcategory .book li h4:hover{
  color: #ff7c2d;
}

.morebookcategory .book li .info{
  margin: 15px 20px 0 25px;
}
.morebookcategory .book li .info span{
  font-size: 16px;
  color: #DD0000;
}

.morebookcategory .page{
  width: 50%;
  margin: 0 auto;
}
</style>
