<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!--  el-rowel-col-->
      <el-row :gutter="20">
        <el-col :span="5">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getBooklist">
            <el-button slot="append" icon="el-icon-search" @click="getBooklist"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="goAddBook"></el-button>
        </el-col>
      </el-row>
      <el-table :data="booklist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="book_img" label="" width="120px">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.book_img"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="book_name" label=""></el-table-column>
        <el-table-column prop="author" label=""></el-table-column>
        <el-table-column prop="book_man" label=""></el-table-column>
        <el-table-column prop="price" label="" width="50px"></el-table-column>
        <el-table-column prop="introduction" label="">
          <template slot-scope="scope">
            <div class="introduction">
              {{scope.row.introduction}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="total" label="" width="100px"></el-table-column>
        <el-table-column prop="sales" label="" width="100px"></el-table-column>
        <el-table-column prop="add_time" label="" width="140px">
          <template slot-scope="scope">
            <div style="font-size: 10px">
              {{ scope.row.add_time|dateFormat1 }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="250px">
          <template slot-scope="scope">
            <!---->
            <el-button type="success" @click="getBookDetails(scope.row.book_id)"></el-button>
            <!---->
            <el-button type="danger" @click="removeBookById(scope.row.book_id)"></el-button>

          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>

      <!---->
      <el-dialog title="" :visible.sync="editDialogVisible" @close="editDialogClosed">
        <el-form :model="book" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="right"
                 :inline="true">
          <el-table-column label="" name="0">
            <el-form-item label="" prop="book_name">
              <el-input v-model="book.book_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="author">
              <el-input v-model="book.author"></el-input>
            </el-form-item>
            <el-form-item label="" prop="book_man">
              <el-input v-model="book.book_man"></el-input>
            </el-form-item>
            <el-form-item label="" prop="price">
              <el-input v-model="book.price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="total">
              <el-input v-model="book.total" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="add_time">
              <el-date-picker v-model="book.add_time" type="datetime" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="book.sales" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="add_time">
              <el-select v-model="book.category_id" placeholder="" filterable>
                <el-option v-for="item in categoryList" :key="item.id" :label="item.category_name"
                           :value="item.category_id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="introduction" style="width: 500px">
              <el-input type="textarea" v-model="book.introduction" style="width: 400px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="book_img">
              <el-input v-model="book.book_img" style="width: 400px;"></el-input>
            </el-form-item>
          </el-table-column>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editBookInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 10,
      },
      //
      booklist: [],
      //
      total: 0,
      //
      book: {},
      //
      categoryList: [],
      //
      editDialogVisible: false,
      //
      editFormRules: {
        book_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 15,
            message: '2~15',
            trigger: 'blur'
          }
        ],
        author: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 6,
            message: '2~6',
            trigger: 'blur'
          }
        ],
        book_man: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            message: '',
            trigger: 'blur'
          }
        ],
        price: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          }
        ],
        total: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          }
        ],
        introduction: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //
    this.getBooklist()
  },
  methods: {
    //
    async getBooklist() {
      this.queryInfo.query=this.queryInfo.query.trim()
      await this.$http.get('/public/getBookList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.booklist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getBooklist()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getBooklist()
    },
    //
    goAddBook() {
      this.$router.push('/adminbook/goAddBook')
    },
    //id
    getBookDetails(id) {
      this.$http.get('/admin/getBookDetails?book_id=' + id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.book = res.data.data.book
        this.categoryList = res.data.data.categoryList
        this.editDialogVisible = true
      })
    },
    //id
    async removeBookById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err=>err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }
      this.$http.post('/admin/removeBookById?book_id='+id).then(res=>{
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('')
        }
        this.$message.success('')
        this.queryInfo.query=''
        this.getBooklist()
      })
    },
    //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //
    editBookInfo(){
        this.$http.post('/admin/editBookInfo',this.book,{emulateJSON: true}).then(res=>{
          if (res.data.code!==200||res.data.data<=0){
            return this.$message.error('')
          }
          this.$message.success('')
          this.editDialogVisible=false
          this.getBooklist()
        })
    }
  },
}
</script>

<style scoped>
.introduction{
  overflow: hidden;
  text-overflow: ellipsis;
  /*  */
  display: -webkit-box;
  /*  */
  -webkit-line-clamp: 5;
  /*  */
  -webkit-box-orient: vertical;
}

.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
