<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>

      <!---->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!--tab
        formtabs tab-paneformtabs
        label-position
        :before-leave: false  Promise  reject
        @tab-click="tabClicked":tab-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-position="left" :inline="true">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="book_name">
              <el-input v-model="addForm.book_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="author">
              <el-input v-model="addForm.author"></el-input>
            </el-form-item>
            <el-form-item label="" prop="book_man">
              <el-input v-model="addForm.book_man"></el-input>
            </el-form-item>
            <el-form-item label="" prop="price">
              <el-input v-model="addForm.price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="total">
              <el-input v-model="addForm.total" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="add_time">
              <el-date-picker v-model="addForm.add_time" type="datetime" placeholder=""></el-date-picker>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-select v-model="addForm.category_id" placeholder="" filterable>
              <el-option v-for="item in categoryList" :key="item.id" :label="item.category_name"
                         :value="item.category_id">
              </el-option>
            </el-select>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-upload :action="uploadURL"
                       :on-preview="handlePreview"
                       :on-remove="handleRemove"
                       list-type="picture" :on-success="handleSuccess" :limit='1' v-model="addForm.book_img"
                       name="bookImg">
              <el-button size="small" type="primary"></el-button>
              <!--<div slot="tip" class="el-upload__tip">jpg/png500kb</div>-->
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-form-item label="" prop="introduction">
              <el-input type="textarea" :rows="2" placeholder=""
                        v-model="addForm.introduction" :autosize="{ minRows: 2, maxRows: 4}">
              </el-input>
              <el-button type="primary" class="btnAdd" @click="add" style="margin-top: 46px"></el-button>
            </el-form-item>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="250px">
      <img :src="previewPath" class="previewImg"/>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //tabs
      activeIndex: '0',
      //
      addForm: {
        //
        book_name: '',
        //
        author: '',
        //
        book_man: '',
        //
        price: '',
        //
        category_id: '',
        //
        total: '',
        //
        book_img: '',
        //
        add_time: '',
        //
        introduction: '',
      },
      //
      addFormRules: {
        book_name: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 15,
            message: '2~15',
            trigger: 'blur'
          }
        ],
        author: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            max: 6,
            message: '2~6',
            trigger: 'blur'
          }
        ],
        book_man: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            message: '',
            trigger: 'blur'
          }
        ],
        price: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          }
        ],
        total: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          }
        ],
        introduction: [
          {
            required: true,//
            message: '',
            trigger: 'blur'//
          },
          {
            min: 2,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      //
      categoryList: [],
      //
      uploadURL: 'http://localhost:8080/admin/uploadBookImg',
      //
      previewVisible: false,
      //url
      previewPath: '',
    }
  },
  created() {
    //
    this.getCategoryList()
  },
  methods: {
    //
    //activeNamename  oldActiveName
    beforeTabLeave(activeName, oldActiveName) {
      //
      if (oldActiveName === '0' && this.addForm.book_name == '' || this.addForm.author == '' ||
        this.addForm.book_man == '' || this.addForm.price == '' || this.addForm.total == '' || this.addForm.add_time == '') {
        this.$message.info('')
        return false
      }

      if (oldActiveName === '1' && this.addForm.category_id == '') {
        this.$message.info('')
        return false
      }

      if (oldActiveName === '2' && this.addForm.book_img == '') {
        this.$message.info('')
        return false
      }
    },
    //
    getCategoryList() {
      this.$http.get('/admin/addBookGetCategoryList').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.categoryList = res.data.data
      })
    },
    //
    //file
    handlePreview(file) {
      this.previewVisible = true
    },
    //
    //file
    handleRemove(file) {
      this.addForm.book_img = ''
    },
    //
    //response
    handleSuccess(response) {
      this.previewPath = response.upload
      this.addForm.book_img = response.upload
    },
    //
    add() {
      if (this.addForm.introduction === '') {
        return this.$message.info('')
      }
      this.$refs.addFormRef.validate(valid => {
        if (!valid) {
          return this.$message.error('')
        }
        this.$http.post('/admin/addBook', this.addForm, {emulateJSON: true}).then(res => {
            console.log(res)
          if (res.data.code!==200||res.data.data<=0){
            return this.$message.error('')
          }
          this.$message.success('')
          this.$router.push('/adminbook')
        })
      })
    },
    //

  }
}
</script>

<style scoped>
.el-steps {
  width: 50%;
  margin: 0 auto;
  margin-top: 10px;
}

.el-card {
  margin-top: 10px;
}
</style>
