<!---->
<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-alert title="" type="info" center show-icon></el-alert>
      <!--  border:stripe-->
      <el-table :data="commentList" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="book_name" label=""></el-table-column>
        <el-table-column prop="user_name" label=""></el-table-column>
        <el-table-column prop="comment_time" label="">
          <template slot-scope="scope">
            {{ scope.row.comment_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="comment_info" label=""></el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <!---->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeCommentById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        //
        pagenum: 1,
        //
        pagesize: 10
      },
      //
      commentList: [],
      //
      total: 0,
    }
  },
  created() {
    //
    this.getUserCommentList();
  },
  methods: {
    //
    getUserCommentList() {
      this.$http.get('/admin/getUserCommentList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('')
        }
        this.commentList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserCommentList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserCommentList()
    },
    //
    async removeCommentById(id){
      const confirmResult=await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).catch(err=>err)

      //confirm,cancel
      if (confirmResult !== 'confirm') {//
        return this.$message.info('')
      }

      this.$http.post('/admin/removeCommentById?id='+id).then(res=>{
        console.log(res)
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getUserCommentList()
      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-alert{
  margin-bottom: 10px;
}
</style>
