<template>
  <div>
    <!---->
    <div class="booklist clearfix" v-if="booklist.length!==0">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{queryInfo.query}}</el-breadcrumb-item>
      </el-breadcrumb>
      <a href="/"></a>
      <ul class="clearfix">
        <li v-for="item in booklist" :key="item.book_id" @click="goBookInfo(item.book_id)">
          <img :src="item.book_img">
          <h4>{{item.book_name}}</h4>
          <div class="info">
            <span>
               {{item.price}}
            </span>
          </div>
        </li>
      </ul>
      <!---->
      <el-row>
        <el-col :span="24" style="text-align: center">
          <div class="page">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[20,30,40,50,60]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </el-col>
      </el-row>
      <HomeFooter></HomeFooter>
    </div>
    <!---->
    <div v-if="booklist.length===0">
      <div class="tips-words">
          <i></i>
        <div>
          <h3><a href="/"></a></h3>
          <dl>
            <dt>:</dt>
            <dd>1.</dd>
            <dd>2.</dd>
            </dl>
        </div>
      </div>
      <HomeFooter></HomeFooter>
    </div>
  </div>


</template>

<script>
import HomeFooter from "./HomeFooter";
export default {
  name: "BookList",
  data() {
    return {
      //
      queryInfo: {
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 20,
      },
      //
      booklist: [],
      //
      total: 0,
    }
  },
  created() {
    //
    this.getBooklist()
  },
  methods: {
    //
    getBooklist() {
      if (this.$route.query.book_keyword!=null||this.$route.query.book_keyword!==undefined){
        this.queryInfo.query=this.$route.query.book_keyword
      }
      this.$http.get('/public/getBookList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          this.$message.error("")
        }
        this.booklist = res.data.data.list
        this.total=res.data.data.total
      })
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getBooklist()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getBooklist()
    },
    //id
    goBookInfo(book_id){
      this.$router.push('/bookinfo?book_id='+book_id)
    }
  },
  components:{
    HomeFooter
  }
}
</script>

<style scoped>
.tips-words{
  width: 1200px;
  height: 300px;
  margin: 0 auto;
  margin-top: 10px;
  background-color: #FFF5ED;
}

.tips-words i{
  width: 54px;
  height: 68px;
  margin: 116px 0 116px 60px;
  background-position: -239px -152px;
  display: block;
  float: left;
  padding:0 20px 0 0;
  background: url("https://res.suning.cn/project/search/searchpage_new/css/images/pic.png?20210126") -165px -153px no-repeat;;
}

.tips-words div{
  float: left;
  margin-top: 114px;
}

.tips-words div h3{
  color: #666666;
}

.tips-words div dl{
  color: #666666;
  font-size: 12px;
}

.tips-words div dl dd em a{
  color: #666666;
  font-style: normal;
}



.booklist {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.booklist a{
  display: block;
  height: 20px;
  text-align: right;
  padding-right: 36px;
  color: #646464;
}

.booklist ul {
  width: 1200px;
  margin-top: 10px;
}

.booklist ul li {
  float: left;
  width: 225px;
  height: 300px;
  background-color: #FFFFFF;
  margin-right: 8px;
  margin-bottom: 15px;
  border: 1px solid #f3f3f3;
}

.booklist ul li:hover {
  z-index: 2; /**/
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1); /**/
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0); /**/
  transform: translate3d(0, -2px, 0);
}

.booklist ul li > img {
  width: 150px;
  height: 150px;
  margin: 20px 0 10px 35px;
}

.booklist ul li h4 {
  height: 46px;
  padding: 10px 20px 0;
  font-size: 12px;
  font-weight: 400;
  /**/
  overflow: hidden;
  /**/
  text-overflow: ellipsis;
  /*  */
  display: -webkit-box;
  /*  */
  -webkit-line-clamp: 3;
  /*  */
  -webkit-box-orient: vertical;
}

.booklist ul li h4:hover {
  color: #ff7c2d;
}

.booklist ul li .info {
  margin: 15px 20px 0 25px;
}

.booklist ul li .info span {
  font-size: 16px;
  color: #DD0000;
}

/**/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}
</style>
